@charset "UTF-8";
.taro_page,
page,
.van-tabs {
  background-color: #fff;
}

page,
.taro_page {
  min-height: 100vh;
}

.doge {
  width: 5.97333rem;
  height: 3.072rem;
  margin-top: 0.17067rem;
  border-radius: 0.17067rem;
  overflow: hidden;
}

.pull-container {
  height: calc(100vh - 4.26667rem);
}

.min-pull-container {
  height: calc(100vh - 1.96267rem);
}

.header {
  padding: 0.256rem;
  background: #ffffff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}
.header .left {
  -webkit-flex: auto;
          flex: auto;
}
.header .right {
  -webkit-flex: none;
          flex: none;
  padding-left: 0.256rem;
}

.placeholder {
  padding-top: 30vh;
  text-align: center;
  color: #ccc;
}
.placeholder .loadingWrapper {
  font-size: 0.512rem;
  margin-bottom: 0.512rem;
}

.footer {
  line-height: 1.06667rem;
  font-size: 0.64rem;
  background-color: lightgreen;
  color: #fff;
  text-align: center;
  padding-bottom: constant(safe-area-inset-bottom);
  /* 兼容 iOS < 11.2 */
  padding-bottom: env(safe-area-inset-bottom);
  /* 兼容 iOS >= 11.2 */
}

.scroll-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.scroll-container .scroll-container-content {
  -webkit-flex: 1;
          flex: 1;
  overflow: scroll;
}
.scroll-container .scroll-container-content > scroll-view, .scroll-container .scroll-container-content > taro-scroll-view-core {
  height: 100%;
}