.van-search {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
  padding: 0.42667rem 0.512rem;
  padding: var(--search-padding, 0.42667rem 0.512rem);
}
.van-search__content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1;
          flex: 1;
  padding-left: 0.512rem;
  padding-left: var(--padding-sm, 0.512rem);
  border-radius: 0.08533rem;
  border-radius: var(--border-radius-sm, 0.08533rem);
  background-color: #f7f8fa;
  background-color: var(--search-background-color, #f7f8fa);
}
.van-search__content--round {
  border-radius: 21.312rem;
  border-radius: var(--border-radius-max, 21.312rem);
}
.van-search__label {
  padding: 0 0.21333rem;
  padding: var(--search-label-padding, 0 0.21333rem);
  font-size: 0.59733rem;
  font-size: var(--search-label-font-size, 0.59733rem);
  line-height: 1.45067rem;
  line-height: var(--search-input-height, 1.45067rem);
  color: #323233;
  color: var(--search-label-color, #323233);
}
.van-search__field {
  -webkit-flex: 1;
          flex: 1;
}
.van-search__field__left-icon {
  color: #969799;
  color: var(--search-left-icon-color, #969799);
}
.van-search--withaction {
  padding-right: 0;
}
.van-search__action {
  padding: 0 0.34133rem;
  padding: var(--search-action-padding, 0 0.34133rem);
  font-size: 0.59733rem;
  font-size: var(--search-action-font-size, 0.59733rem);
  line-height: 1.45067rem;
