/* stylelint-disable no-descending-specificity, selector-max-type */
.van-field {
  --cell-icon-size: 0.68267rem;
  --cell-icon-size: var(--field-icon-size, 0.68267rem);
}
.van-field__label {
  color: #646566;
  color: var(--field-label-color, #646566);
}
.van-field__label--disabled {
  color: #c8c9cc;
  color: var(--field-disabled-text-color, #c8c9cc);
}
.van-field__body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.van-field__body--textarea {
  box-sizing: border-box;
  padding: 0.128rem 0;
  line-height: 1.2em;
  min-height: 1.024rem;
  min-height: var(--cell-line-height, 1.024rem);
}
.van-field__body--textarea textarea {
  resize: none;
}
.van-field__control:empty + .van-field__control {
  display: block;
}
.van-field__control {
  position: relative;
  display: none;
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  padding: 0;
  line-height: inherit;
  text-align: left;
  background-color: transparent;
  border: 0;
  resize: none;
  color: #323233;
  color: var(--field-input-text-color, #323233);
  height: 1.024rem;
  height: var(--cell-line-height, 1.024rem);
  min-height: 1.024rem;
  min-height: var(--cell-line-height, 1.024rem);
}
.van-field__control:empty {
  display: none;
}
.van-field__control--textarea {
  height: 0.768rem;
  height: var(--field-text-area-min-height, 0.768rem);
  min-height: 0.768rem;
  min-height: var(--field-text-area-min-height, 0.768rem);
}
.van-field__control--error {
  color: #ee0a24;
  color: var(--field-input-error-text-color, #ee0a24);
}
.van-field__control--disabled {
  background-color: transparent;
  opacity: 1;
  color: #c8c9cc;
  color: var(--field-input-disabled-text-color, #c8c9cc);
}
.van-field__control--center {
  text-align: center;
}
.van-field__control--right {
  text-align: right;
}
