  min-height: var(--cell-line-height, 1.024rem);
}
.van-field .autosize {
  height: auto;
}
.van-field .autosize textarea {
  resize: none;
  overflow: hidden;
}
.van-field__placeholder {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  pointer-events: none;
  color: #c8c9cc;
  color: var(--field-placeholder-text-color, #c8c9cc);
}
.van-field__placeholder--error {
  color: #ee0a24;
  color: var(--field-error-message-color, #ee0a24);
}
.van-field__icon-root {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  min-height: 1.024rem;
  min-height: var(--cell-line-height, 1.024rem);
}
.van-field__clear-root,
.van-field__icon-container {
  line-height: inherit;
  vertical-align: middle;
  padding: 0 0.34133rem;
  padding: 0 var(--padding-xs, 0.34133rem);
  margin-right: -0.34133rem;
  margin-right: -var(--padding-xs, 0.34133rem);
}
.van-field__clear-root,
.van-field__icon-container,
.van-field__button {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}
.van-field__clear-root {
  font-size: 0.68267rem;
  font-size: var(--field-clear-icon-size, 0.68267rem);
  color: #c8c9cc;
  color: var(--field-clear-icon-color, #c8c9cc);
}
.van-field__icon-container {
  font-size: 0.68267rem;
  font-size: var(--field-icon-size, 0.68267rem);
  color: #969799;
  color: var(--field-icon-container-color, #969799);
}
.van-field__icon-container:empty {
