  margin-top: 0.17067rem;
  margin-top: var(--padding-base, 0.17067rem);
  color: #646566;
  color: var(--field-word-limit-color, #646566);
  font-size: 0.512rem;
  font-size: var(--field-word-limit-font-size, 0.512rem);
